import React from 'react'
import { Platform, Dimensions, Text, TouchableOpacity, View } from 'react-native'
import { createAppContainer } from 'react-navigation'
import { createStackNavigator } from 'react-navigation-stack'
// Import vector icons
import { Icon } from 'react-native-elements'

import NavigationService from '../Services/NavigationService'

import UserFrontScreen from '../Containers/Cloud/UserPanel/FrontScreen'
import UserDashBoardScreen from '../Containers/Cloud/UserPanel/Dashboard'
import UserPigsScreen from '../Containers/Cloud/UserPanel/Pigs'
import UserPensScreen from '../Containers/Cloud/UserPanel/Pens'
import UserBatchsScreen from '../Containers/Cloud/UserPanel/Batchs'
import UserCurvesScreen from '../Containers/Cloud/UserPanel/GrowthCurves'
import AutoPigScreen from '../Containers/Cloud/UserPanel/AutoPig'

import UserOperationsScreen from '../Containers/Cloud/UserPanel/Operations'
import Weighing from '../Containers/Cloud/UserPanel/Weighing'

import UserDataEditor from '../Containers/Cloud/UserPanel/DataEditor'
import UserDataDisplay from '../Containers/Cloud/UserPanel/DataDisplay'
import UserDataPlotter from '../Containers/Cloud/UserPanel/DataPlotter'

import WebViewer from './WebViewer'

import { ApplicationStyles } from '../Theme'

function ViewTransformer (Main, Title) {
  const window = Dimensions.get("window");
  const screen = Dimensions.get("screen");
  
  var wide = window.width>900?true:false
  var App = (Platform.OS === 'ios' || Platform.OS === 'android')||!wide

  var NavComponent = {}
  if (App) {
    NavComponent.screen = Main
    NavComponent.navigationOptions = () => ({
      title: Title,
    })
  } else {
    NavComponent.screen = WebViewer
    NavComponent.navigationOptions = () => ({
      title: Title,
      headerMode: App?'screen':'none',
      headerShown: App?true:false,
    })
  }

  return NavComponent
}

/**
 * The root screen contains the application's navigation.
 *
 * @see https://reactnavigation.org/docs/en/hello-react-navigation.html#creating-a-stack-navigator
 */
const StackNavigator = createStackNavigator(
  {
    // Create the application routes here (the key is the route name, the value is the target screen)
    // See https://reactnavigation.org/docs/en/stack-navigator.html#routeconfigs
    
    UserFront: {
      screen: WebViewer,
      navigationOptions: () => ({
        title: 'Front Pannel',
        headerMode: (Dimensions.get("window").width < 900 || (Platform.OS === 'ios' || Platform.OS === 'android'))?'screen':'none',
        headerShown: (Dimensions.get("window").width < 900 || (Platform.OS === 'ios' || Platform.OS === 'android'))?true:false,
        headerLeft: (props) => (
          <View style={{padding:6, paddingLeft: 15}}>
            <Icon type='ionicon' name="arrow-back" size={25} color="black" onPress={() => {
              if (props.label === "System Board") {
                NavigationService.nacigationPop()
              }
              else NavigationService.navigate('MainStack')
            }}/>
          </View>
        ),
      }),
      params: (Dimensions.get("window").width < 900 || (Platform.OS === 'ios' || Platform.OS === 'android'))?{Main: UserFrontScreen}:{Menu: UserFrontScreen, Main: UserDashBoardScreen},
      path: 'user_front',
    },

    UserDashBoard: {
      ...ViewTransformer(UserDashBoardScreen, 'Dashboard'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserDashBoardScreen},
        path: 'user_dashboard',
      }
    },

    UserPigs: {
      ...ViewTransformer(UserPigsScreen, 'Pigs'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserPigsScreen},
        path: 'user_pigs',
      }
    },

    UserPens: {
      ...ViewTransformer(UserPensScreen, 'Pens'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserPensScreen},
        path: 'user_pens',
      }
    },

    UserBatchs: {
      ...ViewTransformer(UserBatchsScreen, 'Batchs'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserBatchsScreen},
        path: 'user_batchs',
      }
    },

    UserCurves: {
      ...ViewTransformer(UserCurvesScreen, 'Curves'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserCurvesScreen},
        path: 'user_curves',
      }
    },
    
    AutoPig: {
      ...ViewTransformer(AutoPigScreen, 'AutoPig'), 
      ...{
        params: {Menu: UserFrontScreen, Main: AutoPigScreen},
        path: 'user_autopigs',
      }
    },

    /**************** Operations */
    UserOperations: {
      ...ViewTransformer(UserOperationsScreen, 'Operations'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserOperationsScreen},
        path: 'user_operations',
      }
    },

    WeighingOperations: {
      ...ViewTransformer(Weighing, 'Operations'), 
      ...{
        params: {Menu: UserFrontScreen, Main: Weighing},
        path: 'weight_operations',
      }
    },

    /**************** Data Displays */
    DataEditor: {
      ...ViewTransformer(UserDataEditor, 'User Data'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserDataEditor},
        path: 'user_dataedtior',
      }
    },

    DataDisplay: {
      ...ViewTransformer(UserDataDisplay, 'User Data'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserDataDisplay},
        path: 'user_datadisplay',
      }
    },

    DataPlotter: {
      ...ViewTransformer(UserDataPlotter, 'Batch Data'), 
      ...{
        params: {Menu: UserFrontScreen, Main: UserDataPlotter},
        path: 'user_dataplotter',
      }
    },
  },
  {
    initialRouteName: 'UserFront', 

    // See https://reactnavigation.org/docs/en/stack-navigator.html#stacknavigatorconfig
    headerMode: 'float',
    defaultNavigationOptions:{
      headerTitleAlign: 'center',
      headerTitleStyle:{...ApplicationStyles.navigationHeader},
      headerRight: () => (<View style={{padding:6}}></View>),
    },
  }
)

export default StackNavigator
