import React, { useEffect, useState, Component } from 'react'
import { Platform, Dimensions, Text, View, ActivityIndicator, Image } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../Services/NavigationService'

//import DeviceActions from '../Stores/Device/Actions'
import FirebaseActions from '../Stores/Firebase/Actions'

import { Button_Panel } from '../Components'
import { ApplicationStyles, Helpers, Images } from '../Theme'

class ViewContainer extends Component {
  constructor(props) {
    super(props);
    
    const window = Dimensions.get("window");
    const screen = Dimensions.get("screen");

    this.state = {
      dimensions: {window, screen},
      viewState: 0,

      ShowMainError: false,
      FocusSubscription: null,

      NaviParams: props.navigation.state.params
    }
  }

  setData(Props) {
  }

  getData() {
  }

  checkData(props) {
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.viewError != prevProps.firebase.viewError) {
        this.forceUpdate()
      }
    }
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidMount() {
    Dimensions.addEventListener("change", this.onChange);

    this.props.setFirebaseError(null)
    this.checkData(this.props);
    this.getData();

    const FocusSubscription = this.props.navigation.addListener(
      'willFocus',
      payload => {
        this.props.setViewLoading(false)
        this.props.setViewError(null)
      }
    );

    this.setState({
      FocusSubscription: FocusSubscription,
    })
  }

  componentWillUnmount() {
    if (this.state.FocusSubscription) {
      this.state.FocusSubscription.remove()
    }
    Dimensions.removeEventListener("change", this.onChange);
  }

  render() {
    const ErrorMessage = this.props.firebase.viewError
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    var wide = this.state.dimensions.window.width>900?true:false
    var App = (Platform.OS === 'ios' || Platform.OS === 'android')||!wide

    var Menu = this.state.NaviParams.Menu
    var Main = this.state.NaviParams.Main

    if (Menu) {
      if (App) {
        return(
          <Main/>
        )
      } else {
        return(
          <View style={[Helpers.rowMain, Helpers.fullSize, {alignItems:'flex-start'}]}>
            <View style={[Helpers.fillRow, Helpers.fullSize, {flex:1, minWidth:390, maxWidth:400, marginHorizontal: -30, borderRightWidth:1}]}>
              {/* Menu */}
              <Menu/>
            </View>
            <View style={[Helpers.fillRow, Helpers.fullSize, {flex:1, alignItems:'flex-start', marginLeft:20}]}>
              {/* View */}
              <Main/>
            </View>
          </View>
        )
      }
    } else {
      return(
        <Main/>
      )
    }
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ViewContainer);
//export default ViewContainer;
