import * as React from 'react'
import { useEffect, Component } from 'react'
import { Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, FlatList, SafeAreaView } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, ConsysRefs, GetCurve_FromBatch, CurvePredict, GetBatchEnd, AccumulateTime, format_Time, GetStats_FromBatch, GetStats_FromPen } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

import { VictoryBar, VictoryLine, VictoryScatter, VictoryChart, VictoryTheme, VictoryVoronoiContainer, VictoryZoomContainer, createContainer, VictoryTooltip, VictoryAxis, VictoryLegend } from "victory-native";
const VictoryZoomVoronoiContainer = createContainer("zoom", "voronoi");

function getRandomNumber(low, high) {
  var r = Math.floor(Math.random() * (high - low + 1)) + low;
  return r;
}

function getRandomColor() {
  var red_range = [10, 250];
  var green_range = [10, 250];
  var blue_range = [10, 250];
  var a_range = [0.2, 1];

  var red = getRandomNumber(red_range[0], red_range[1]);
  var green = getRandomNumber(green_range[0], green_range[1]);
  var blue = getRandomNumber(blue_range[0], blue_range[1]);

  var alpha = getRandomNumber(a_range[0] * 100, a_range[1] * 100) / 100;

  return {
    r: red,
    g: green,
    b: blue,
    a: alpha,
    rgbaValue: 'rgba(' + red + ',' + green + ',' + blue + ',' + alpha + ')'
  }
}

class AutoPig extends Component {
  constructor(props) {
    super(props);

    var { FarmData, ValveData, ScaleData, categories, series, Domain } = this.setData(props)
    this.state = {
      FarmData: FarmData,
      ValveData: ValveData,
      ScaleData: ScaleData,

      Limit: 300,
      SysId: 2,
      STime: undefined,
      ETime: undefined,
      LastId: undefined,

      categories: categories,
      series: series,

      entireDomain: Domain,
      zoomedDomain: Domain
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;
    const FirebaseUser = this.props.firebase.firebaseUser;
    var Site = null, refToken = null, accessToken = null;
    var FarmData = null, ValveData = {}, ScaleData = {};
    var categories = [], series = [];

    if (FirebaseUser && FirebaseUser.farms && localDataStore && localDataStore.farms) {
      const FarmsData = localDataStore.farms
      if (FarmsData && FarmsData[Object.keys(FirebaseUser.farms)[0]]) {
        FarmData = FarmsData[Object.keys(FirebaseUser.farms)[0]]
        /*if (FarmData.site && FarmData.refToken) {
          Site = FarmData.site
          refToken = FarmData.refToken
          if (FarmData.token) accessToken = FarmData.token
        }*/
      }
    }
    if (localDataStore && localDataStore.consys) {
      if (localDataStore.consys.feedData) {
        ValveData = localDataStore.consys.feedData

        var DataSet = []
        Object.keys(ValveData).filter(e => (e !== '__jsogObjectId')).forEach(element => {
          var data = ValveData[element]

          var _DataSet = DataSet.find(x => x.name === data.valve)
          if (_DataSet) {

            _DataSet.data.push({x:(data.timestamp*1000), y: data.amount , silono:data.silono, unit:data.unit})
          } else {
            //series.push({name:data.valve, type:"column", data:[{x:data.timestamp, y:data.amount}]})
            DataSet.push({
              name:data.valve,
              data: [{x:(data.timestamp*1000), y:data.amount, silono:data.silono, unit:data.unit}]
            })
          }
        });
        DataSet.forEach(element => {
          var color = getRandomColor().rgbaValue
          series.push(
            {
              type: "Bar",
              name: element.name,
              color: color,
              data: element.data
            }
          )
          categories.push({name: element.name, symbol: { fill: color}})
        });
      }
      if (localDataStore.consys.scaleData){
        ScaleData = localDataStore.consys.scaleData

        var DataSet2 = []
        Object.keys(ScaleData).filter(e => (e !== '__jsogObjectId')).forEach(element => {
          var data = ScaleData[element]
          DataSet2.push({x: (data.timestamp*1000), y:data.amount})
        });
        if (DataSet2.length > 0) {
          //series.push({name:'AutoScale', type:"line", data:DataSet2})
          //var color = getRandomColor().rgbaValue
          series.push(
            {
              type: 'Line',
              name: 'AutoScale',
              color: getRandomColor().rgbaValue,
              data: DataSet2
            }
          )
        }
      }
    }
    //console.log(series)
    var Domain = this.getEntireDomain(series)
    return { FarmData, ValveData, ScaleData, categories, series, Domain }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;
    const FirebaseUser = this.props.firebase.firebaseUser;

    this.props.getFarm()

    /*if (FirebaseUser && FirebaseUser.farms && localDataStore && localDataStore.farms) { 
      const FarmsData = localDataStore.farms
      if (FarmsData && FarmsData[Object.keys(FirebaseUser.farms)[0]]) {
        const FarmData = FarmsData[Object.keys(FirebaseUser.farms)[0]]
        if (FarmData.site && FarmData.refToken) {
          this.props.getAutoPig()
        }
      }
    }*/
  }

  checkData(props) {
    const localDataStore = this.props.firebase.localDataStore;
    const FirebaseUser = this.props.firebase.firebaseUser;

    //THIS check is used many times and in future wil be unnessasary as this page is not shown unless all is true
    //Can provoke loopback pages..
    if (FirebaseUser && FirebaseUser.farms && localDataStore && localDataStore.farms) { 
      const FarmsData = localDataStore.farms
      if (FarmsData && FarmsData[Object.keys(FirebaseUser.farms)[0]]) {
        const FarmData = FarmsData[Object.keys(FirebaseUser.farms)[0]]
        if (!FarmData.site || !FarmData.refToken) {
          NavigationService.nacigationPop()
        }
      }
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.localDataStore && prevProps.firebase.localDataStore) {
        const localDataStore = this.props.firebase.localDataStore;
        const firebaseUser = this.props.firebase.firebaseUser
        
        if (localDataStore.farms && firebaseUser.farms && localDataStore.farms[Object.keys(firebaseUser.farms)[0]]) {
          var Farm = localDataStore.farms[Object.keys(firebaseUser.farms)[0]]
          if ((this.state.FarmData.site !== Farm.site || this.state.FarmData.refToken !== Farm.refToken || this.state.FarmData.token !== Farm.token) ||
              (localDataStore.consys && localDataStore.consys.scaleData && JSON.stringify(this.state.ScaleData) !== JSON.stringify(localDataStore.consys.scaleData)) ||
              (localDataStore.consys && localDataStore.consys.feedData && JSON.stringify(this.state.ValveData) !== JSON.stringify(localDataStore.consys.feedData))) {
           
            var { FarmData, ValveData, ScaleData, categories, series, Domain } = this.setData(this.props)
            console.log(ValveData, ScaleData)
            this.setState({
              FarmData: FarmData,
              ValveData: ValveData,
              ScaleData: ScaleData,

              categories:categories,
              series:series,
              
              entireDomain: Domain,
              zoomedDomain: Domain
            })
          }
        }
      }
    }
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  getEntireDomain(series) {
    var MinMax = {y:[0,0], x: [0,0]}
    series.forEach(element => {
      var data = element.data
      var min = _.minBy(data, d => d.y).y
      var max = _.maxBy(data, d => d.y).y
      if (min < MinMax.y[0]) MinMax.y[0] = min
      if (max > MinMax.y[1]) MinMax.y[1] = max
      min = _.minBy(data, d => d.x).x
      max = _.maxBy(data, d => d.x).x
      if (min < MinMax.x[0] || MinMax.x[0] === 0) MinMax.x[0] = min
      if (max > MinMax.x[1]) MinMax.x[1] = max
    })
    return MinMax
  }

  getZoomDomain(){
    const zoomedXDomain = this.state.zoomedDomain.x;
    const Serie = this.state.series;
    const maxPoints = 150;

    const filteredSeries = [];
    Serie.forEach(element => {
      var filtered = []
      if (element.type === "Line") {
        var last = null;
        element.data.forEach(data => {
          if (data.x >= zoomedXDomain[0]) {
            if (data.x <= zoomedXDomain[1]) {
              if (filtered.length === 0 && last !== null) filtered.push(last)
              filtered.push(data)
            } else if (last.x <= zoomedXDomain[1]) filtered.push(data)
          }
          last = data
        })
      } else {
        filtered = element.data.filter(
          (d) => (d.x >= zoomedXDomain[0] && d.x <= zoomedXDomain[1])
        );
      }
      /*if (filtered.length > maxPoints ) {
        const k = Math.ceil(filtered.length / maxPoints);
        return filtered.filter(
          (d, i) => ((i % k) === 0)
        );
      }*/
      if (filtered.length > 0) filteredSeries.push({...element, data: filtered})
    });
    return filteredSeries;
  }

  onDomainChange(domain) {
  	this.setState({
    	zoomedDomain: domain,
    });
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    const localDataStore = this.props.firebase.localDataStore

    var Token = (this.state.FarmData.token)?this.state.FarmData.token:null
    var test = ""

    const renderData = this.getZoomDomain();
    const Legends = [this.state.zoomedDomain.x[0]]
    while(Legends[Legends.length-1] < this.state.zoomedDomain.x[1]){
      Legends.push(Legends[Legends.length-1] + 3600000)
    }

    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
            {ErrorMessage ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
            ):(<></>)}
            <TitleElement Text={"AutoPigs"}/>
            <View>
              <View style={[Helpers.rowCenter]}>
                <Input label="SysId"
                  containerStyle={[{maxWidth:100}]}
                  style={[ApplicationStyles.input,{maxWidth:90}]}
                  onChangeText={text => {
                    if (!isNaN(text)) {
                      this.setState({
                        SysId:text
                      })
                    }
                  }}
                  value={this.state.SysId}
                />
                <Input label="Data Limit"
                  containerStyle={[{maxWidth:150}]}
                  style={[ApplicationStyles.input,{maxWidth:140}]}
                  onChangeText={text => {
                    if (!isNaN(text) || text === "") {
                      if (text === "") text = undefined
                      this.setState({
                        Limit:text
                      })
                    }
                  }}
                  value={this.state.Limit}
                />
                <Input label="LastId (Ids newer than)"
                  containerStyle={[{maxWidth:200}]}
                  style={[ApplicationStyles.input,{maxWidth:190}]}
                  onChangeText={text => {
                    if (text === "") text = undefined
                    if (text) {
                      //if (DataStringCheck(text)){
                        this.setState({LastId:text})
                      //}
                    }else {
                      this.setState({LastId:text})
                    }
                  }}
                  value={this.state.LastId}
                />
              </View>
              <View style={[Helpers.rowCenter]}>
                <Input label="Start Time : Epoch-sec (newest)"
                  containerStyle={[Helpers.halvWidth]}
                  style={[ApplicationStyles.input]}
                  onChangeText={text => {
                    if (text === "") {
                      this.setState({
                        STime:undefined
                      })
                    }
                    else if (!isNaN(text)) {
                      this.setState({
                        STime:text
                      })
                    }
                  }}
                  value={this.state.STime}
                />
                <Input label="End Time : Epoch-sec (oldest)"
                  containerStyle={[Helpers.halvWidth]}
                  style={[ApplicationStyles.input]}
                  onChangeText={text => {
                    if (text === "") {
                      this.setState({
                        ETime:undefined
                      })
                    }
                    else if (!isNaN(text)) {
                      this.setState({
                        ETime:text
                      })
                    }
                  }}
                  value={this.state.ETime}
                />
              </View>
              <View style={[Helpers.rowCenter]}>
                <Button Text="Fetch Scale_Data"
                  onPress={() => {
                    this.props.getAutoPig(this.state.Limit, this.state.SysId, this.state.STime, this.state.ETime, null, this.state.LastId)
                  }}/>
                <View style={{width:20}}/>
                <Button Text="Fetch Valve_Data"
                  onPress={() => {
                    this.props.getValveFeed(this.state.Limit, this.state.SysId, this.state.STime, this.state.ETime, null, this.state.LastId)
                  }}/>
              </View>
            </View>
            <SafeAreaView style={[Helpers.borderContainer, Helpers.fullSize, {maxWidth:900, maxHeight: 600 + (this.state.categories.length?22*(this.state.categories.length%15):0)}]}>
              <VictoryChart
                scale={{ x: "time" }}
                containerComponent={
                  <VictoryZoomVoronoiContainer voronoiDimension="x"
                    zoomDimension="x"
                    mouseFollowTooltips
                    onZoomDomainChange={this.onDomainChange.bind(this)}
                    labels={({ datum }) => {
                      if (datum.childName === "none") return;
                      var DateTime = new Date(datum.x)
                      var format = DateTime.getDate() + "/" + DateTime.getMonth() + "/" + DateTime.getFullYear() + " " + + ("0" + DateTime.getHours()).slice(-2) + ":" + ("0" + DateTime.getMinutes()).slice(-2)
                      if (datum.silono || datum.unit){
                        var add = "\n"
                        if (datum.silono) add += 'Silo: ' + datum.silono + ' '
                        if (datum.unit) add += 'Unit: ' + datum.unit
                        return datum.childName + ' y: ' + datum.y + add + "\n" + format
                      }
                      else return datum.childName + ' y: ' + datum.y + "\n" + format
                    }}
                    labelComponent={<VictoryTooltip cornerRadius={0} flyoutStyle={{fill: "white"}}/>}
                  />
                }
                width={900}
                height={600}
                domain={this.state.entireDomain}
                theme={VictoryTheme.material}
              >
                <VictoryAxis crossAxis={false}
                  scale={{ x: "time" }}
                  tickCount={8}
                  minDomain={{ x: 1 , y: 0}}
                  tickFormat={(t) => {
                    var DateTime = new Date(t)
                    var format = DateTime.getDate() + "/" + DateTime.getMonth() + "/" + DateTime.getFullYear() + " " + ("0" + DateTime.getHours()).slice(-2) + ":" + ("0" + DateTime.getMinutes()).slice(-2)
                    if (test === format) return;
                    else test = format
                    return format
                  }}
                  theme={VictoryTheme.material}
                  standalone={false}
                  fixLabelOverlap
                  tickValues={Legends}
                  style={{
                    grid: { stroke: "#818e99", strokeWidth: 0.5 },
                  }}
                />
                <VictoryAxis dependentAxis crossAxis={false}
                  tickCount={20}
                  minDomain={{ x: 0 , y: 0}}
                  tickFormat={(t) => {
                    return Math.round(t * 10) / 10
                  }}
                  theme={VictoryTheme.material}
                  standalone={false}
                  style={{
                    grid: { stroke: "#818e99", strokeWidth: 0.5 },
                  }}
                />
                {renderData.map((Data, i) => {
                  if (Data.type === "Bar" && !Data.deactivate){
                    return (<VictoryBar key={"Bar_" + Data.name}
                      barWidth={ 6 }
                      data={ Data.data }
                      style={{data: { fill: Data.color},}}
                      name={ 'valve: ' + Data.name }
                    />);
                  } else if (Data.type === "Line"&& !Data.deactivate){
                    return (<VictoryLine key={"Line_" + Data.name}
                      data={ Data.data }
                      style={{
                        data: { stroke: Data.color, strokeWidth: ({ active }) => active ? 2 : 1},
                      }}
                      name={ Data.name }
                    />);
                  }
                })}
              </VictoryChart>
              {this.state.categories.length > 0?(
                <VictoryLegend
                  width={880}
                  height={22*(this.state.categories.length%15)}
                  orientation="horizontal"
                  itemsPerRow={14}
                  gutter={20}
                  style={{ border: { stroke: "black" } }}
                  data={this.state.categories}
                  events={[{
                    target: "data",
                    eventHandlers: {
                      onClick: () => {
                        return [
                          {
                            target: "data",
                            mutation: (props) => {
                              var series = this.state.series;
                              var index = series.findIndex(i => i.name === props.datum.name);
                              if (index !== -1) {
                                series[index].deactivate = !series[index].deactivate;
                              }
                              this.setState({
                                series: series
                              })
                              const fill = props.style && props.style.fill;
                              return fill === "#000" ? null : { style: { fill: "#000" } };
                            }
                          }, {
                            target: "labels",
                            mutation: (props) => {
                              //return props.text === "clicked" ? null : { text: "clicked" };
                            }
                          }
                        ];
                      }
                    }
                  }]}
                />
              ):(<></>)}
            </SafeAreaView>
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}
//<CurveElement Display={true} DataPoints={DataSet} Height={600} Width={1250}/>
//<Chart options={this.state.options} series={this.state.series} width={1240} height={700} />

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),
    
    getFarm: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {})),
    getValveFeed: (limit, sysId, start, stop, offset, lastId) => dispatch(FirebaseActions.actionStarting(ConsysRefs.VALVEFEED, DataRefs.CONSYS, {limit:limit, sysId:sysId, start:start, stop:stop, offset:offset, lastId:lastId})),
    getAutoPig: (limit, sysId, start, stop, offset, lastId) => dispatch(FirebaseActions.actionStarting(ConsysRefs.SCALE, DataRefs.CONSYS, {limit:limit, sysId:sysId, start:start, stop:stop, offset:offset, lastId:lastId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(AutoPig);
//export default AutoPig;